// アプリ全体のスタート
class App extends React.Component {
  // 初期値の設定
  constructor (props) {
    super(props)
    this.state = {
      types:[,,,,,,,,,,,,],
      uri: 'https://pokeapi.co/api/v2/type/1',
      ryota_img_uri: 'https://dic.nicovideo.jp/oekaki/31320.png',
      ryota: {
        name: null,
        id: null,
        stats: null,
        types: [null, null],
        stats: {
          hp: 0,
          attack: null,
          defense: null,
          'special-attack': null,
          'special-defense': null
        }
      },
      you_img_uri: 'https://dic.nicovideo.jp/oekaki/129162.png',
      you: {
        name: null,
        id: null,
        stats: null,
        types: [null, null],
        stats: {
          hp: 0,
          attack: null,
          defense: null,
          'special-attack': null,
          'special-defense': null
        }
      },
      ryotaoryou: true,
      pokemons: [
        {
          name: null,
          imagurl: null,
          id: null
        }
      ],
      transdata: {}
    }
    
    this.types = []
    this.stats = ['hp', '']

    this.uri = 'https://pokeapi.co/api/v2/'
    this.transuri="https://gist.githubusercontent.com/PonDad/93922f63c3143489e30c3716d3d176d2/raw/0ea137397f9701828ecd7da7d253168678646488/pokemon.json"
    this.imguri =
      'https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/'
    this.backimguri = 'https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/back/'
  }

  // データを取ってくる
  async getType() {
    const data = await window
      .fetch('./type.json')
      .then(res => res.json())
      .then(res => res.total)
    this.setState({types:data})
  }

  // 最初にデータを取ってくる
  componentWillMount(){
    this.getType()
  }

  // 次にデータを取ってくる
  componentDidMount () {
    this.getSerchData(this.state.uri)
    this.getTransData(this.transuri)
    document.title = "VS良太"
  }

  // ポケモンのデータ取得
  async getSerchData (uri) {
    const data = await window
      .fetch(uri)
      .then(res => res.json())
      .then(json => json.pokemon)
    this.getImage(data)
  }

  // 翻訳のデータ取得
  async getTransData (uri){
    const data = await window
      .fetch(uri)
      .then(res => res.json())
    this.setState({transdata: data})
  }

  // 写真のデータ取得
  getImage (data) {
    const pokemons = []
    data.map((v, i) => {
      const name = v.pokemon.name
      const splits = v.pokemon.url.split('/')
      const imgurl = this.imguri + splits[6] + '.png'
      const pokemondata = { 
        name: name,
        imgurl: imgurl,
        id: splits[6]
      }
      if (splits[6].length < 5) {
        pokemons.push(pokemondata)
      }
    })
    this.setState({ pokemons: pokemons })
  }

  // 取得したデータの登録
  async submit (id) {
    const uri = 'https://pokeapi.co/api/v2/pokemon/' + id
    const data = await window.fetch(uri).then(res => res.json())
    let name = data.name.charAt(0).toUpperCase() + data.name.slice(1)
    this.state.transdata.map((v) => {
      if (v.en === name){
        name = v.ja
      }
    })
    const idd = data.id
    const img = this.imguri + id + '.png'
    const img_back = this.backimguri + id + '.png'
    const stats = {}
    data.stats.map(v => {
      stats[`${v.stat.name}`] = v.base_stat
    })
    const types = []
    const types_number = []
    data.types.map(v => {
      this.state.types.map((value, i) => {
        if (value.en == v.type.name){
          types.push(value.ja)
          types_number.push(i)
        }
      })
    })
    const total = {
      name,
      idd,
      img,
      img_back,
      stats,
      types,
      types_number
    }
    if (this.state.ryotaoryou) {
      this.setState({ ryotaoryou: false, ryota: total })
    } else {
      this.setState({ ryotaoryou: true, you: total })
    }
  }

  // 出力==========================================
  render () {
    return (
      <>
        <ViewHead />
        <div className="text">
          <ViewSetting ryotaoryou={this.state.ryotaoryou} />
        </div>
        <ViewSerchText onChange={this.handleTextIn.bind(this)} />
        <ViewButtonSubmit on={this.handleTextOut.bind(this)} />
        <GetInputType
          type={this.state.types}
          typemit={this.handleTypeSubmit.bind(this)}
        />
        <ViewImages
          data={this.state.pokemons}
          onClick={this.submit.bind(this)}
        />
        <div className="playername">
          <h2>良太:</h2>
        </div>
        <ViewPlayer data={this.state.ryota}
          playerimg={this.state.ryota_img_uri}
        />
        <div className="playername">
          <h2>あなた:</h2>
        </div>
        <ViewPlayer data={this.state.you}
          playerimg={this.state.you_img_uri}
        />
        <Game data={this.state} />
        <div className="footer">
          <ul class="nav">
            <p className="cp">使用したサイト</p>
              <li><a href="https://pokeapi.co/">PokeApi</a></li>
              <li><a href="https://gist.github.com/PonDad/93922f63c3143489e30c3716d3d176d2">日本語変換</a></li>
              <li><a href="https://dic.nicovideo.jp/a/%E3%80%90%E3%83%89%E3%83%83%E3%83%88%E3%80%91%E8%A3%BD%E3%82%AB%E3%83%96%E8%A8%98%E4%BA%8B%E3%80%90%E3%83%9D%E3%82%B1%E3%83%A2%E3%83%B3%E5%9B%B3%E9%91%91%E3%83%BB%E4%BA%BA%E7%89%A9%E7%B7%A8%E3%80%91">写真</a></li>
            <p className="cp">&copy; VS良太</p>
          </ul>
        </div>
      </>
    )
  }

  // inputのデータの取得
  handleTextIn (event){
    this.setState({serchkeyword: event.target.value})
  }

  // inputのデータを送る
  handleTextOut(){
    let key = null
    if (isFinite(this.state.serchkeyword)) {
      this.submit(this.state.serchkeyword)
    }else {
      this.state.transdata.map((v,i) => {
        if (v.ja === this.state.serchkeyword){
          key = i + 1
          this.submit(key)
        }
      })
    }
  }

  // データを取ってくるものに送る
  handleTypeSubmit (i) {
    this.getSerchData(this.uri + 'type/' + i)
  }
}

